#content-area {
  overflow: auto;
  width: 100%;
  background: whitek;
  min-height: 100vh;
}

#filter {
  background: $grey;
  width: 30%;
  max-width: 300px;
  float: left;
  .inside {
    padding: 20px;
    max-width: 300px;
  }
  h4 {
    font-size: 0.9rem;
    margin-bottom: 25px;
    font-weight: 500;
    text-transform: uppercase;
  }
  select {
    background: transparent;
    padding: 10px;
    font-size: 14px;
    font-weight: 500;
    color: #1b1b1b;
    display: block;
    margin-bottom: 25px;
    width: 100%;
  }

  .filters {
    .title {
      font-size: 0.9rem;
      display: block;
      margin-bottom: 1rem;
      color: $darkgrey;
    }
    input[type="text"] {
      border-radius: 5px;
      font-size: 1rem;
      padding: 0.4rem;
      display: inline-block;
      width: 45%;
      margin-right: 2.5%;
      margin-bottom: 1rem;
    }
    &.extras {
      label {
        display: block;
        width: 100%;
        padding: 10px 0;
        input[type="checkbox"] {
          float: right;
        }
      }
    }
  }
}

#listings {
  display: inline-block;
  width: 70%;
  padding: 15px;
  background: $offwhite;

  .search-area {
    input[type="text"] {
      width: 70%;
      background: transparent;
      font-size: 2rem;
      border: none;
      border-bottom: 2px solid $darkgrey;
      margin-bottom: 20px;
      font-weight: 100;
      color: black;
    }
  }
  .sortby-area {
    margin-bottom: 20px;
    padding-bottom: 35px;
    .results {
      display: inline-block;
      color: $darkgrey;
      font-size: 0.8rem;
    }
    .sort-options {
      float: right;
      select {
        font-size: 0.9rem;
        font-weight: 300;
        border: 1px solid $lightgrey;
        background: transparent;
        padding: 20px;
        display: inline-block;
        margin-right: 15px;
        vertical-align: top;
      }
      .view {
        display: inline-block;
        .fas {
          font-size: 1.5rem;
          margin-right: 15px;
        }
      }
    }
  }
  .listing-results {
    margin-bottom: 60px;
    .listing {
      background: #fff;
      border-radius: 5px;
      box-shadow: 0px 9px 54px -4px rgba(224, 224, 224, 1);
      -webkit-box-shadow: 0px 9px 54px -4px rgba(224, 224, 224, 1);
      -moz-box-shadow: 0px 9px 54px -4px rgba(224, 224, 224, 1);
    }
    .listing-img {
      color: white;
      padding: 15px;
      position: relative;
      width: 100%;
      height: 100%;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
      min-height: 200px;
      -webkit-border-top-left-radius: 5px;
      -webkit-border-top-right-radius: 5px;
      -moz-border-radius-topleft: 5px;
      -moz-border-radius-topright: 5px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      transition: all 0.4s ease-in-out;
      &:hover {
        .details {
          opacity: 1;
        }
        .address {
          opacity: 0;
        }
      }
      .address {
        text-transform: capitalize;
        font-size: 1.3rem;
        font-weight: 600;
        position: absolute;
        bottom: 20px;
        text-shadow: 0px 1px 3px rgba(91, 91, 91, 1);
        transition: all 0.4s ease-in-out;

      }
      .details {
        transition: all 0.4s ease-in-out;
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(75, 0, 226, 0.801);
        background: -moz-linear-gradient(
          -45deg,
          rgba(75, 0, 226, 0.8) 0%,
          rgba(41, 137, 216, 0.8) 50%,
          rgba(32, 124, 202, 0.8) 51%,
          rgba(255, 137, 255, 0.8) 100%
        ); /* FF3.6-15 */
        background: -webkit-linear-gradient(
          -45deg,
          rgba(75, 0, 226, 0.8) 0%,
          rgba(41, 137, 216, 0.8) 50%,
          rgba(32, 124, 202, 0.8) 51%,
          rgba(255, 137, 255, 0.8) 100%
        ); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(
          135deg,
          rgba(75, 0, 226, 0.8) 0%,
          rgba(41, 137, 216, 0.8) 50%,
          rgba(32, 124, 202, 0.8) 51%,
          rgba(255, 137, 255, 0.8) 100%
        ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4b00e2', endColorstr='#ff89ff',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
        -webkit-border-top-left-radius: 5px;
        -webkit-border-top-right-radius: 5px;
        -moz-border-radius-topleft: 5px;
        -moz-border-radius-topright: 5px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;

        .user-img {
          width: 35px;
          height: 35px;
          border-radius: 50%;
          border: 2px solid white;
          display: inline-block;
          background: url("https://post.medicalnewstoday.com/wp-content/uploads/sites/3/2020/03/GettyImages-1092658864_thumb-732x549.jpg")
            no-repeat center center;
          -webkit-background-size: cover;
          -moz-background-size: cover;
          -o-background-size: cover;
          background-size: cover;
        }
        .user-details {
          display: inline-block;
          vertical-align: top;
          .user-name {
            display: block;
            color: rgba(255, 255, 255, 1);
            font-size: 0.9rem;
            font-weight: 300;
            margin-top: 3px;
            margin-bottom: 3px;
            margin-left: 10px;
          }
          .post-date {
            display: block;
            color: rgba(255, 255, 255, 0.7);
            font-size: 0.9rem;
            margin-left: 10px;
          }
        }
        .listing-details {
          margin-top: 20px;
          margin-bottom: 15px;
          .fas {
            display: inline-block;
            font-size: 20px;
            margin-right: 15px;
          }
          span {
            font-size: 0.8rem;
            font-weight: 300;
            display: inline-block;
            vertical-align: top;
            position: relative;
            top: 3px;
          }
          .floor-space {
            margin-bottom: 10px;
          }
        }
      }
    }
  }
  .bottom-info {
    padding: 20px 15px;
    .price {
      font-size: 1.1rem;
      display: block;
      margin-bottom: 10px;
      color: $darkgrey;
    }
    .location {
      font-size: 1.3rem;
      display: block;
      color: $lightgrey;
    }
  }
}
.view-btn {
  background: $pink;
  padding: 8px;
  font-size: 0.9rem;
  border-radius: 5px;
  text-align: center;
  max-width: 150px;
  transition: background 0.4s ease-in-out;
  &:hover {
    background: black;
    cursor: pointer;
  }
}

#pagination {
  width: 100%;
  display: block;
  text-align: center;
  .pages {
    display: inline-block;
    background: white;
    box-shadow: 0px 9px 54px -4px rgba(224, 224, 224, 1);
    -webkit-box-shadow: 0px 9px 54px -4px rgba(224, 224, 224, 1);
    -moz-box-shadow: 0px 9px 54px -4px rgba(224, 224, 224, 1);
    li {
      padding: 20px 30px;
      display: inline-block;
      &:hover,
      &.active {
        background: $pink;
        color: white;
        font-weight: 1200;
        cursor: pointer;
      }
    }
  }
}
