header {
    background: #0C002B;
    color: white;
    overflow: auto;
    padding: 20px 15px;
    .logo {
        float: left;
    }
    nav {
        float: right;
        a{
            padding: 10px 20px;
            color: white;
            text-decoration: none;
            text-transform: capitalize;
            font-weight: 400;
            font-size: .9rem;
            &:hover {
                color: #FF718E;
            }
            &.register-btn {
                background: $pink;
                padding: 7px 25px;
                text-align: center;
                color: white;
                border-radius: 5px;
            }
        }
    }
}